.content-wrap{
  padding: 30px 40px;
  @media (width<500px) {
    padding: 30px 10px;
  }
  color: #262626;
  font-size: 14px;
  flex-grow: 10;
  max-width: 100%;
  .title {
    margin-bottom: 30px;
  }
  p,
  li {
    line-height: 2.2em;
  }
  .Eg {
    flex-grow: 10;
    display: flex;
    flex-direction: column;
    gap: 20px;
    border: 1px solid rgba(100,100,100,.2);
    padding: 50px 20px;
    @media (width<500px) {
      padding: 50px 5px;
    }
    margin-bottom: 30px;
    overflow-x: auto;
      >.showEg {
        flex-grow: 10;
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        margin-bottom: 30px;
        max-width: 100%;
        //border: 1px solid rgba(100,100,100,.2);
        justify-content: center;
        width: 100%;
        user-select: auto;
        padding: 50px 10px;
      }

  }
  > .showCode {
    margin-bottom: 30px;

  }
  .interface {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-bottom: 80px;
    max-width: 100%;
    overflow-x: auto;
  }
}
